}

stencil-route-link a {
  color: inherit;
}

hr {
  border: none;
  height: 1px;
  background: var(--c-indigo-30);
  margin: var(--space-6) 0;
}

.ui-blockquote {
  background: #f2f5f8;
  border-radius: 4px;
  position: relative;
  padding: 64px 80px 40px 111px;
  color: #5e749a;
  font-family: "Adobe Caslon", Georgia, Times, "Times New Roman", serif;
  font-style: italic;
  border: none;
  margin: 77px -16px 54px;
}
.ui-blockquote:before {
  position: absolute;
  top: -6px;
  left: 54px;
  font-size: 180px;
  content: "â€œ";
  color: #e3e7ec;
}
.ui-blockquote > * {
  position: relative;
  z-index: 1;
}

.ui-breadcrumbs {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}
.ui-breadcrumbs li {
  display: inline-block;
}
.ui-breadcrumbs li:last-child a {
  color: #010610;
}
.ui-breadcrumbs li + li::before {
  content: "/";
  display: inline-block;
  font-size: 16px;
  font-weight: 400;
  color: rgba(65, 77, 92, 0.2);
  margin: 0 6px;
}
.ui-breadcrumbs a {
  color: var(--c-indigo-60);
}

.ui-breakpoint {
  display: none;
}

@media (min-width: 1216px) {
  .ui-breakpoint-xl {
    display: var(--display);
  }
}

@media (min-width: 1024px) and (max-width: 1215px) {
  .ui-breakpoint-lg {
    display: var(--display);
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .ui-breakpoint-md {
    display: var(--display);
  }
}

@media (min-width: 640px) and (max-width: 767px) {
  .ui-breakpoint-sm {
    display: var(--display);
  }
}

@media (max-width: 639px) {
